<template>
    <!-- <div style="height:100vh;"> -->
        <GoogleMapLoader style="height:100vh;" v-if="destinations!=null" class="mb-3" :mapConfig="mapConfig" :destinations="destinations" :driver="driver"/>
    <!-- </div> -->
</template>
<script>
import GoogleMapLoader from '@/components/Map/GoogleMapLoader';
import Pusher from 'pusher-js';
export default {
    name:"MapDetails",
    components:{
        GoogleMapLoader
    },
    data(){
        return {
            mapConfig:{
                zoom: 15,
                center: {
                    lng: 116.23,
                    lat: -3.23,
                },
                gestureHandling: "greedy",
                mapTypeId: 'hybrid',
                disableDefaultUI: true,
                zoomControl: true,
            },
            destinations:null,
            driver:null,
            pusher:null
        }
    },
    created(){
        
        this.getDirections();
        this.pusher = new Pusher(process.env.VUE_APP_PUSHER_KEY, {
            cluster: 'ap1',
            forceTLS: true
        });
        var channel = this.pusher.subscribe('TriggerRute' + this.id_rute);
        channel.bind('EventTriggerRute', () => {
            this.getDirections();
        });
    },
    methods:{
        getDirections(){
            this.axios.get(this.$store.state.url_api + "transaction/directions/" + this.$route.params.transaction_number, {
                headers:{
                    "Authorization" : "Bearer " + localStorage.token
                }
            }).then(response => {
                this.destinations = response.data.data.directions;
                this.driver = response.data.data.driver
                // this.calculateAndDisplayRouteForCart(response.data.data.directions.origin, response.data.data.directions.destination, response.data.data.directions.waypoints); 
            })
        }
    }
}
</script>